.download-app {
  position: relative;
  z-index: 1;
  background-color: $dark-blue;

  @media (min-width: $mobile-width) {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-height: 735px;
    background-color: $dark;
    background-image: url("../img/iphone-hand-mobile@1x.png"),
                      url("../img/bg-back-mobile@1x.jpg");
    background-position: 0% 39%, 0% 0%;
    background-repeat: no-repeat, no-repeat;
    background-size: auto auto,
                     auto 462px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/iphone-hand-mobile@2x.png"),
                          url("../img/bg-back-mobile@2x.jpg");
        background-position: 0% 39%, 0% 0%;
        background-repeat: no-repeat, no-repeat;
        background-size: 260px 290px,
                         auto 462px;
      }
  }

  @media (min-width: $tablet-width) {
    min-height: 729px;
    background-color: $dark;
    background-image: url("../img/iphone-hand-tablet@1x.png"),
                      url("../img/bg-back-tablet@1x.jpg");
    background-position: 60% 100%, 50% 50%;
    background-repeat: no-repeat, no-repeat;
    background-size: 720px 550px,
                     auto 730px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/iphone-hand-tablet@2x.png"),
                          url("../img/bg-back-tablet@2x.jpg");
        background-position: 60% 100%, 50% 50%;
        background-repeat: no-repeat, no-repeat;
        background-size: 720px 550px,
                         auto 730px;
      }
  }

  @media (min-width: $desktop-width) {
    min-height: 850px;
    background-color: $dark;
    background-image: url("../img/iphone-hand-desktop@1x.png"),
                      url("../img/bg-back-desktop@1x.jpg");
    background-position: 60% 100%, 54% 50%;
    background-repeat: no-repeat, no-repeat;
    background-size: 1000px 640px,
                     cover;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/iphone-hand-desktop@2x.png"),
                          url("../img/bg-back-desktop@2x.jpg");
        background-position: 60% 100%, 54% 50%;
        background-repeat: no-repeat, no-repeat;
        background-size: 1000px 640px,
                         cover;
      }
  }
}

.download-app__wrapper {
 width: 90%;
 margin: 0 auto;

 @media (min-width: $mobile-width) {
   position: relative;
   top: 30px;
  }

 @media (min-width: $tablet-width) {
   top: 50px;
  }
}

.download-app__content--wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $mobile-width) {
   min-height: 610px;
  }

  @media (min-width: $tablet-width) {
   width: 48%;
   min-height: 400px;
  }

  @media (min-width: $desktop-width) {
   width: 350px;
   min-height: 500px;
  }
}

.download-app__button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $white;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: bold;
  line-height: 18px;
  height: 55px;
  border: 3px solid $white;
  box-sizing: border-box;
  border-radius: 29px;
  background-color: transparent;
  cursor: pointer;

   @media (min-width: $mobile-width) {
    width: 280px;
    font-size: 18px;
    padding: 18px 30px 17px 30px;
   }

   @media (min-width: $tablet-width) {
    width: 300px;
    font-size: 18px;
    padding: 18px 45px 18px 45px;;
   }

   @media (min-width: $desktop-width) {
    height: 56px;
    font-size: 21px;
    padding: 18px 28px 18px 28px;
   }
}

.download-app__slogan {
  font-family: "OpenSans", "Arial", sans-serif;
  color: $white;
  text-align: center;
  font-weight: 300;
  margin: 0;

  @media (min-width: $mobile-width) {
    font-size: 24px;
    line-height: 120%;
  }

  @media (min-width: $tablet-width) {
    font-size: 40px;
    max-width: 185px;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68px;
    max-width: 230px;
  }
}

.download-app__list {
  @include list();
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 205px;

  &--wrapper {
    display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

    @media (min-width: $mobile-width) {
      min-height: 210px;
    }

    @media (min-width: $tablet-width) {
      min-height: 235px;
    }

    @media (min-width: $desktop-width) {
      min-height: 275px;
    }

  }
}

.download-app__content {
 font-weight: 300;
 color: $white;
 line-height: 24px;
 text-align: center;
 margin: 0;

  @media (min-width: $mobile-width) {
    font-size: 14px;
  }

  @media (min-width: $tablet-width) {
    font-size: 18px;
  }

}

.download-app__button:hover {
  color: $pink;
  background-color: $white;
}

.download-app__button:active {
  color: transparentize($pink, 0.7);
  background-color: $white;
}
